<script setup>
import { computed, ref, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";

// Comps
import CodeEditor from "@/components/app/code-editor/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Consts
import { snippet, timeFormatters, getErrorTypeColor } from "./constants.js";
const route = useRoute();
const router = useRouter();
const jsoneditor = ref(null);

const returnData = () => {
  const errorCode = snippet.value.meta?.snippet_errors?.[route.params.errorindex];
  return errorCode ? JSON.stringify(errorCode, null, 2) : "['Couldn't find error']";
};

const currentError = computed(() => {
  return snippet.value.meta?.snippet_errors?.[route.params.errorindex] || {};
});
</script>

<template>
  <div class="grow shrink overflow-hidden flex flex-col">
    <div class="col-span-3 flex flex-row items-center p-6 border-b border-zinc-200 dark:border-zinc-700 gap-3">
      <div class="rounded px-1 text-xs border" :class="getErrorTypeColor(currentError.type)">{{ currentError.type }}</div>

      <div class="truncate text-sm">{{ timeFormatters.short(currentError.timestamp) }}</div>

      <RouterLink :to="`/snippets/${route.params.id}`" class="ml-auto">
        <AppButton type="transparent" class="text-sm">
          <AppIcon icon="close" />
        </AppButton>
      </RouterLink>
    </div>
    <template v-if="snippet?.meta?.snippet_errors">
      <CodeEditor ref="jsoneditor" :value="returnData()" language="json" class="grow shrink overflow-hidden" />
    </template>
  </div>
</template>
